module.exports = "<div class=\"panel-body\">\n  <div class=\"ed-sidebar editor-tab\">\n    <span [ngClass]=\"{active: (selected=='settings')}\" class=\"circle\"></span>\n    <a (click)=\"onSelect('settings')\" id=\"config-settings\" href=\"#b-setting\" data-toggle=\"tab\" class=\"section-head gen-setting\"><i class=\"material-icons\">settings_applications</i> General & SEO Settings\n      <!--<label class=\"ed-sidebar-subhead ellipsis\">Basic Settings</label>-->\n    </a>\n  </div>\n  <div class=\"ed-sidebar editor-tab\">\n    <span [ngClass]=\"{active: (selected=='email')}\" class=\"circle\">&nbsp;</span>\n    <a (click)=\"onSelect('email')\" href=\"#email-setting\" data-toggle=\"tab\" class=\"section-head gen-setting\"><i class=\"material-icons\">email</i>\n      Confirmation Email\n      <!--<label class=\"ed-sidebar-subhead ellipsis\">That Will Be Sent To The User</label>-->\n    </a>\n  </div>\n  <div class=\"ed-sidebar editor-tab\">\n    <span [ngClass]=\"{active: (selected=='integrations')}\" class=\"circle\"></span>\n    <a (click)=\"integrationClick($event);onSelect('integrations')\" [attr.href]=\"isIntegerationAvailable?'#integration-setting':'#b-setting'\"\n       data-toggle=\"tab\" class=\"section-head gen-setting\"><i class=\"material-icons\">dialpad</i> Integrations <span class=\"comming-soon-tag\"> Coming Soon</span>\n      <!--<label class=\"ed-sidebar-subhead ellipsis\">Send data to 500+ web apps</label>-->\n    </a>\n  </div>\n  <div class=\"ed-sidebar editor-tab\">\n    <span [ngClass]=\"{active: (selected=='share-your-calculator')}\" class=\"circle\"></span>\n    <a (click)=\"onSelect('share-your-calculator')\" id=\"config-share-your-calculator\" href=\"#cal-setting\" data-toggle=\"tab\" class=\"section-head gen-setting\"><i class=\"material-icons\">share</i> Sharing\n      <!--<label class=\"ed-sidebar-subhead ellipsis\">Generate A URL And Share It</label>-->\n    </a>\n  </div>\n  <div class=\"ed-sidebar editor-tab\">\n    <span [ngClass]=\"{active: (selected=='launch-popup')}\" class=\"circle\"></span>\n    <a (click)=\"onSelect('launch-popup')\" href=\"#popup-setting\" data-toggle=\"tab\" class=\"section-head gen-setting\"><i class=\"material-icons\">launch</i> Launch in a popup\n      <!--<label class=\"ed-sidebar-subhead ellipsis\">Open It In A Web Page</label>-->\n    </a>\n  </div>\n  <div class=\"ed-sidebar editor-tab\">\n    <span [ngClass]=\"{active: (selected=='embedded-code')}\" class=\"circle\"></span>\n    <a (click)=\"embeddingClick($event);onSelect('embedded-code')\" [attr.href]=\"isEmbeddCodeAvailable?'#code-setting':'#b-setting'\"\n       data-toggle=\"tab\" class=\"section-head gen-setting\"><i class=\"material-icons\">desktop_mac</i> Embed on a Webpage\n      <!--<label class=\"ed-sidebar-subhead ellipsis\">Generate It In Your Domain</label>-->\n    </a>\n  </div>\n</div>\n"

