module.exports = "<div class=\"config-template-tabs new-email-outr\">\n    <div class=\"col-xs-12 col-sm-7 col-sm-offset-1 np\">\n        <div class=\"col-xs-12 launch-top\">\n            <h3>Launch in a Pop-up</h3>\n        </div>\n        <div class=\"col-xs-12 popup-choose\">\n            <h4>What kind of a pop up do you prefer?</h4>\n        </div>\n        <div class=\"col-sm-12 col-xs-12 np popup-choose\">\n            <ul>\n                <li class=\"col-sm-4 col-xs-6\">\n                    <input type=\"radio\" id=\"f-option\" name=\"selector2\" (change)=\"isDrawer=!isDrawer\" [checked]=\"!isDrawer\">\n                    <label for=\"f-option\" class=\"check\">\n                        <img src=\"assets/images/popup.png\" />\n                        <p>Classic</p>\n                    </label>\n                </li>\n                <li class=\"col-sm-4 col-xs-6\">\n                    <input type=\"radio\" id=\"f-option2\" name=\"selector2\" (change)=\"isDrawer=!isDrawer\" [checked]=\"isDrawer\">\n                    <label for=\"f-option2\" class=\"check\">\n                        <img src=\"assets/images/drawer-icon.png\" />\n                        <p>Drawer</p>\n                    </label>\n                </li>\n            </ul>\n        </div>\n        <div class=\"col-xs-12  col-xs-12 np popup-choose\">\n            <div class=\"col-xs-12 popup-choose\">\n                <h4>Trigger the pop up using a link or a button?</h4>\n            </div>\n            <div class=\"col-sm-12 np popup-choose\">\n                <ul>\n                    <li class=\"col-sm-4 col-xs-6\">\n                        <input type=\"radio\" id=\"f-option3\" name=\"selector\" (change)=\"isButton=!isButton\" [checked]=\"isButton\">\n                        <label for=\"f-option3\" class=\"check\">\n                            <i class=\"material-icons\">play_circle_outline</i>\n                            <p>Button</p>\n                        </label>\n                    </li>\n                    <li class=\"col-sm-4 col-xs-6\">\n                        <input type=\"radio\" id=\"f-option4\" name=\"selector\" (change)=\"isButton=!isButton\" [checked]=\"!isButton\">\n                        <label for=\"f-option4\" class=\"check\">\n                            <i class=\"material-icons\">repeat</i>\n                            <p>Link</p>\n                        </label>\n                    </li>\n                    <li class=\"col-sm-4 col-xs-12\">\n                        <label class=\"buttonlink col-xs-12\">\n                            <p>Button/Link Text</p>\n                            <input type=\"text\" [(ngModel)]=\"buttonName\" placeholder=\"Add text here\" />\n                        </label>\n                    </li>\n                    <li class=\"mobile-show\">\n                        <p>Click on the button/link below to preview the pop-up.</p>\n                        <div class=\"text-center\"><a id=\"popupButton\" [href]=\"(iFrameUrl)\" *ngIf=\"!isDrawer\" (click)=\"togglePopup()\" class=\"fancybox-effects-d fancybox fancybox.iframe btn btn-default btn-sm btn-flat btn-basic\" [class.no-border]=\"!isButton\">{{buttonName}}</a>\n                            <a id=\"drawerButton\" (click)=\"toggleDrawer()\" *ngIf=\"isDrawer\" class=\"btn btn-default btn-sm btn-flat btn-basic\" [class.no-border]=\"!isButton\">{{buttonName}}</a></div>\n                    </li>\n                </ul>\n            </div>\n        </div>\n        <div class=\"col-sm-12 np popup-copypaste\">\n            <div class=\"col-sm-12 np popup-copypaste-top\">\n                <h4>Copy and paste this piece of code and place it in your website</h4>\n                <div class=\"col-sm-12 copypaste-box\">\n                    <div class=\"copypaste-box-scroll\">\n                        <pre>\n                            <code>{{iframeData}}</code>\n                        </pre>\n                    </div>\n                </div>\n            </div>\n        </div>\n    </div>\n    <div class=\"col-sm-2 general-helppanel\">\n        <div class=\"col-xs-12 np text-center\">\n            <label class=\"g-helpicon\">\n                <i class=\"material-icons\">lightbulb_outline</i>\n            </label>\n        </div>\n        <h5>Preview</h5>\n        <p>Click on the button/link below to preview the pop-up.</p>\n        <div class=\"text-center\"><a id=\"popupButton\" [href]=\"(iFrameUrl)\" *ngIf=\"!isDrawer\" (click)=\"togglePopup()\" class=\"fancybox-effects-d fancybox fancybox.iframe btn btn-default btn-sm btn-flat btn-basic\" [class.no-border]=\"!isButton\">{{buttonName}}</a>\n            <a id=\"drawerButton\" (click)=\"toggleDrawer()\" *ngIf=\"isDrawer\" class=\"btn btn-default btn-sm btn-flat btn-basic\" [class.no-border]=\"!isButton\">{{buttonName}}</a></div>\n    </div>\n</div>\n<iframe id=\"drawerIframe\" class=\"hide\" style=\"background:#fff;z-index:99999; width:50%; height:100vh; position:fixed; float:left; top:0; left:0;\" [src]=\"(iFrameUrl) | safeUrl \"></iframe>\n<a title=\"Close\" class=\"drawer-close hide\" id=\"close-drawer\" (click)=\"drawerClose()\" href=\"javascript:;\"></a>\n<div class=\"fancybox-drawer hide\"></div>\n"

