module.exports = "<div class=\"type-details\" >\n  <div class=\"form-label\"> Location:</div>\n  <select\n    class=\"select-default\"\n    (change)=\"onChange($event)\"\n    data-width=\"fit\"\n    class=\"form-control\"\n  >\n    <option  value=\"Landing\" [selected] = \"_JSONBuilder.getSelectedPage().type ==='Landing'\">Welcome screen</option>\n    <!--<option  value=\"Questionnaire\" [selected] = \"_JSONBuilder.getSelectedPage().type ==='Questionnaire' && _JSONBuilder.getSelectedPage().sections[0].type === 'LeadFormQ'\">Before Questions</option>-->\n    <option *ngIf=\"_JSONBuilder.getJSONBuilt().template !=='sound-cloud'\" value=\"Questionnaire\" [selected] = \"_JSONBuilder.getSelectedPage().type ==='Questionnaire' && _JSONBuilder.getSelectedPage().sections[0].type !== 'LeadFormQ'\">After Questions</option>\n    <option  value=\"Result\" [selected] = \"_JSONBuilder.getSelectedPage().type ==='Result'\">Results Page</option>\n  </select>\n</div>\n<div class=\"type-details\">\n  <div>\n    <span class=\"form-label\">Section Title:</span>\n    <textarea class=\"wysiwyg question-textarea\" [(ngModel)]=\"leadSection.title\" ></textarea>\n  </div>\n</div>\n<div class=\"type-details\">\n  <span class=\"form-label\" [class.no-margin]=\"leadSection.showDesc === false\">Help Text:</span>\n  <div class=\"switch\">\n    <label>\n      <input type=\"checkbox\" name=\"properties-checkbox\"\n             class=\"show-check\"\n             [checked]=\"leadSection.showDesc\"\n             (change)=\"onChangeDescription(leadSection);callGA('HELPTEXT',leadSection)\"\n      >\n      <span class=\"lever\"></span>\n    </label>\n  </div>\n  <div class=\"col-xs-12 no-padding text-group div-check\"\n       *ngIf=\"leadSection.showDesc\"\n  >\n    <textarea class=\"form-text question-textarea\" [(ngModel)] = \"leadSection.description\"></textarea>\n  </div>\n</div>\n<div class=\"type-details\" >\n  <div class=\"form-label\" [class.no-margin] = \"editorControl.leadform_question.visible === false\">Fields:</div>\n  <div class=\"switch hide\">\n    <label>\n      <input id=\"lead_gen_control\" type=\"checkbox\" class=\"\" (change)=\"toggleLeadform($event);callGA('LEADGENTOGGLE')\" [checked]=\"leadSection?.visible\">\n      <span class=\"lever\"></span>\n    </label>\n  </div>\n  <div class=\"div-check\" *ngIf=\"leadSection?.visible\">\n    <div class=\"lead-gen\" *ngFor=\"let field of editorControl.leadform_question.fields ; let i = index\">\n      <selectize [field]=\"field\" [index] = \"i\"></selectize>\n      <!-- More button -->\n      <div class=\"btn-group icon-options\">\n        <button type=\"button\" class=\"btn btn-default dropdown-toggle\" data-toggle=\"dropdown\" aria-haspopup=\"true\" aria-expanded=\"false\">\n          <i class=\"material-icons\" style=\"margin-top: 12px;\">more_vert</i>\n        </button>\n        <ul class=\"dropdown-menu\">\n          <li><a href=\"javascript:void(0);\" *ngIf = \"!field.validations.required.status\" (click)=\"toggleRequired(field);callGA('SETMANDATE')\">\n            <i class=\"material-icons\">done_all</i> Set as Mandatory</a></li>\n          <li><a href=\"javascript:void(0);\" *ngIf = \"field.validations.required.status\" (click)=\"toggleRequired(field);callGA('UNSETMANDATE')\">\n            <i class=\"material-icons\">not_interested</i> Unset</a></li>\n          <li><a href=\"javascript:void(0);\" class=\"form-leadform-icon\"\n                 (click)=\"delete_Field_From_LeadForm(i);callGA('DeleteLeadGenField')\">\n            <i class=\"material-icons\">clear</i> Delete</a></li>\n\n        </ul>\n      </div></div>\n    <div class=\"col-md-11 col-xs-11 no-padding pull-right\" [class.hide]=\"editorControl.leadform_question.fields.length>=4\">\n      <a  href=\"javascript:void(0);\" (click)=\"add_Field_In_LeadForm();callGA('ADDLEADFIELD')\"\n          class=\"default add_field_button\">Add a field\n      </a>\n    </div>\n\n  </div>\n</div>\n<div class=\"type-details\" *ngIf=\"leadSection?.visible\">\n  <span class=\"form-label\">Button:</span>\n  <input\n    type=\"text\"\n    class=\"detail-text\"\n    [(ngModel)]=\"editorControl.leadform_question.props.title\"\n  />\n</div>\n"

